<template>
  <iframe v-if="link" class="chat-wrapper" :src="link" ref="iframe" frameborder="0" allowfullscreen></iframe>
</template>

<script>
import themeVariables from '@style/components/game-container.chat.modules.scss'
import { getAvatorBgStyle } from '@/components/ui/userSpriteImage/userSprite'
import { mapState } from 'vuex'
import { getCacheIcon, canvas2Img } from '@/assets/imgCache.js'

export default {
  name: 'ChatIntegrate',
  props: {
    badge: {
      type: Boolean,
      required: true
    }
  },
  data() {
    return {
      link: null,
      containerId: 'ChatIntegrate',
      chatInstance: null,
      user_avator: ''
    }
  },
  computed: {
    ...mapState('base', ['user', 'languageType', 'systemConfig']),
    theme() {
      const img = getCacheIcon('icon_email')
      let base64 = ''
      if (img.status && img.value) {
        base64 = canvas2Img(img.value)
      }
      return {
        '--tg-main-bg': themeVariables.main_bg,
        '--tg-avator-servier': `url(${base64}) center / contain no-repeat`,
        '--tg-avator-user': this.user_avator,
        '--tg-primary': themeVariables.primary_color,
        '--tg-message-time-color': themeVariables.message_time_color,
        '--tg-message-bg': themeVariables.message_bg,
        '--tg-message-title-color': themeVariables.message_title_color,
        '--tg-message-content-color': themeVariables.message_content_color,
        '--tg-problem-view-color': themeVariables.problem_view_color,
        '--tg-problem-link-color': themeVariables.problem_link_color,
        '--tg-input-wrapper-bg': themeVariables.input_wrapper_bg,
        '--tg-input-bg': themeVariables.input_bg,
        '--tg-input-border': themeVariables.input_border,
        '--tg-input-color': themeVariables.input_color,
        '--tg-input-placeholder-color': themeVariables.input_placeholder_color,
        '--tg-fast-bg': themeVariables.fast_bg,
        '--tg-fast-color': themeVariables.fast_color,
        '--tg-fast-child-bg': themeVariables.fast_child_bg
      }
    }
  },
  watch: {
    languageType(val) {
      this.postMessageEvent('updateOptions', { language: val })
    },
    badge(val) {
      if (!val) return
      this.postMessageEvent('message_push')
      this.clearDot()
    }
  },
  created() {
    getAvatorBgStyle(this.user.headimg, 44).then(res => {
      this.user_avator = res
    })
    // 监听订阅事件
    this.initSubscribeMessage()
    // 初始化应用
    this.initRender()
    // 清除红点
    this.badge && this.clearDot()
  },

  beforeDestroy() {
    window.removeEventListener('message', this.handleMessageEvent)
  },
  methods: {
    initRender() {
      const { chat_promote_appid = '', chat_promote_entry_host = '' } = this.systemConfig
      if (!chat_promote_entry_host.trim())
        return this.$pop.message().show({ content: this.$t('UI.Parameter_Error_Text') })

      const paramsData = {
        appid: chat_promote_appid,
        user_id: this.user.uid,
        token: this.user.token,
        language: this.languageType,
        render_nav: false
      }
      const paramsStr = this.$u.lodash.json2String(paramsData, '&')
      let link = chat_promote_entry_host + '?' + paramsStr
      this.link = link
    },
    // 清除红点信息
    clearDot() {
      setTimeout(() => {
        this.$store.dispatch('base/readDotStatus', 'ai_promote')
      }, 3000)
    },
    initSubscribeMessage() {
      window.addEventListener('message', this.handleMessageEvent)
    },
    // 推送事件
    postMessageEvent(eventName, data) {
      const $frame = this.$refs.iframe
      if (!$frame) return
      const message = {
        type: eventName,
        content: data
      }
      $frame.contentWindow.postMessage(message, '*')
    },
    // 监听事件
    handleMessageEvent(e) {
      const { type, content } = e.data
      switch (type) {
        // 初始化
        case 'initialized':
          this.onAppInitialized()
          break
        default:
          break
      }
    },

    onAppInitialized() {
      // 更新主题
      this.postMessageEvent('updateOptions', { theme: this.theme })
      if (this.badge) {
        this.postMessageEvent('message_push')
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.chat-wrapper {
  flex: 1;
  width: 100%;
  overflow: hidden;
}
</style>
