<template>
  <div class="meail_box">
    <div
      v-for="(item, inx) in tabList"
      :class="[
        'meail_box_item',
        { meail_box_item_ative: tabTndex == item.value },
        { meail_box_item__has_tip: item.showDot }
      ]"
      :key="inx"
      @click="emailHall(item)"
    >
      <icon-font class="cursor_point" :name="item.iconName" :size="variables.email_list_head_pc_arrow_size" />
      <div class="meail_box_item_text">{{ item.lable }}</div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import variables from '@style/components/game-container.email-modal.modules.scss'
export default {
  props: {
    tabTndex: {
      type: Number,
      default: 0
    },
    tabList: {
      type: Array,
      default: []
    }
  },
  data() {
    return {
      variables: variables
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType'])
  },
  methods: {
    emailHall(item) {
      this.$emit('emailHall', item)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/game-container.email-modal.modules.scss';
.meail_box {
  height: 47px;
  width: 100%;
  @include setBox(row, center, center);
  background: $meail_box_bg;
  border-bottom: $meail_box_border;
  &_item {
    position: relative;
    height: 100%;
    width: 100%;
    @include setBox(row, center, center);
    flex: 1;
    color: $meail_box_item_color;
    .meail_box_item_text {
      font-size: 12px;
      margin: 0px 0px 0px 6px;
    }
  }
  .meail_box_item_ative {
    margin-bottom: -1px;
    border-bottom: $meail_box_item_ative_border;
    color: $meail_box_item_active_color;
  }
}

.meail_box_item__has_tip {
  &:after {
    position: absolute;
    right: 0;
    top: 2px;
    content: '';
    background: $red_drop;
    width: 9px;
    height: 9px;
    border-radius: 50%;
  }
}
</style>
