<template>
  <custom-dialog v-model="visible" :full="true">
    <email-list />
  </custom-dialog>
</template>

<script>
import { mapState } from 'vuex'
import emailList from './ui/email-list'

export default {
  components: {
    emailList
  },
  data() {
    return {}
  },
  computed: {
    ...mapState('modal', ['emailState']),
    ...mapState('base', ['isApp', 'user', 'languageType']),
    // 是否显示
    visible: {
      get() {
        return this.emailState
      },
      set(val) {
        this.$store.dispatch('modal/setEmailState', val)
      }
    }
  },
  methods: {}
}
</script>

<style lang="scss" scoped></style>
