<template>
  <div class="mes_box" @click="change(item)">
    <div class="bg_box flex_row">
      <icon-font
        :name="!item.status ? 'icon-icon_email_close1' : 'icon-icon_email_open1'"
        :size="variables.email_box_content_left_icon_size"
      ></icon-font>
      <div class="bg_box_rigth">
        <div class="bg_box_rigth_tt">
          <div class="bg_box_rigth_tt_title text_1">{{ item.title }}</div>
          <div class="bg_box_rigth_tt_gold" v-if="item.gold && item.status != 2">
            <icon-font
              name="icon-icon_email_chip"
              :size="variables.email_box_content_right_tt_gold_icon_size"
              class="bg_box_rigth_tt_gold_icon"
            ></icon-font>
            <div class="bg_box_rigth_tt_gold_text">{{ item.gold }}</div>
          </div>
        </div>
        <div class="bg_box_rigth_bt" :class="{ all_text: show }">
          {{ item.content }}
        </div>
      </div>
    </div>
    <div class="box-link text-decoration-underline" v-show="show" @click="externalClick(item)">
      {{ item.url_text }}
    </div>
    <div class="box-view flex1 btn_radius" v-show="show && item.gold">
      <div class="box-view-title">
        <icon-font
          name="icon-icon_email_chip"
          :size="variables.email_box_content_box_view_gold_icon_size"
          class="box-view-icon"
        ></icon-font>
        <div class="box-view-text">{{ item.gold }}</div>
      </div>
      <v-btn
        color="t_btn_host_color"
        :disabled="item.status == 2"
        class="primary_disabled btn_radius box-view-btn"
        @click.stop="receiveAction(item.status)"
      >
        {{ $t('UI_Receber') }}
      </v-btn>
    </div>
    <div class="bottom_box flex_row cursor_point">
      <div class="bottom_box_title">{{ sendTime(item.send_time) }}</div>
      <div class="btn_show">
        <div class="btn_show_text">{{ show ? $t('UI_Btn_Hide') : $t('UI_Btn_Show') }}</div>
        <icon-font
          name="icon-Icon_Down"
          :size="variables.email_box_bottom_box_btn_icon_size"
          :style="{ transform: show ? 'rotate(180deg)' : 'rotate(0deg)' }"
        ></icon-font>
      </div>
    </div>
  </div>
</template>

<script>
import { info } from '@/api/modules/config.js'
import loadIcon from '@/assets/icons.js'
import { mapState } from 'vuex'
import { jump } from '@/utils/linkMap'
import variables from '@style/components/game-container.email-modal.modules.scss'
import { datetime } from '@/utils/helper.js'
export default {
  props: {
    item: {
      type: Object,
      default: () => {}
    }
  },
  data() {
    return {
      variables,
      show: false
    }
  },
  computed: {
    ...mapState('base', ['isApp', 'user', 'languageType'])
  },
  methods: {
    //时间格式化
    sendTime(time) {
      return datetime.formatDateTime(new Date(time * 1000), 'yyyy-MM-dd HH:mm:ss')
    },
    loadIcon(key) {
      return loadIcon(key, false)
    },
    // status 0 未读 1已读未领 2已读已领
    receiveAction(status) {
      if (status == 2) return
      this.$emit('submit', this.item)
    },
    // 查看详情
    async change(item = {}) {
      this.show = !this.show
      if (!this.show || !item.id) return

      // 查询消息状态以及是否已经领取
      const params = {
        token: this.user.token,
        language: this.languageType,
        id: item.id
      }
      const res = await info(params).catch(e => e)
      const { code = 0, msg = '' } = res

      // 更新红点信息
      this.$store.dispatch('base/showRedDotReceivedFun', {
        version: 1,
        event: 'mail',
        language: this.languageType,
        token: this.user.token
      })

      if (code != 0) {
        if (code == 5001) {
          this.$pop.message().show({ content: msg })
          this.$emit('getReSetList')
          return
        } else {
          return
        }
      }
      // 取用 info 里的描述
      const { content = '' } = res.data && this.$u.lodash.isObject(res.data) ? res.data : {}
      item.content = content

      // 更改已读未读状态，status 0 未读 1已读未领 2已读已领
      item.status = item.status == 2 ? item.status : 1
    },
    externalClick(item) {
      jump(item, this)
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/game-container.email-modal.modules.scss';

.primary_disabled.v-btn:disabled {
  opacity: 0.3;
  background-color: $btn_host_color !important;
  color: #fff !important;
}

.flex1 {
  flex: 1;
}

.mes_box {
  background: $email_box_bg;
  font-weight: $email_box_font_weight;
  margin: $email_box_margin;
  width: 100%;
  border-radius: $email_box_app_radius;

  .bg_box {
    padding: $email_box_content_padding;
    &_rigth {
      width: 100%;
      margin: $email_box_content_right_margin;
      &_tt {
        @include setBox(row, center, center);

        &_title {
          flex-shrink: 1;
          flex-grow: 1;
          font-size: $email_box_content_right_tt_title_font_size;
        }

        &_gold {
          flex-shrink: 0;
          flex-grow: 0;
          @include setBox(row, space-between, center);
          background: $email_box_content_right_tt_gold_box_bg;
          width: $email_box_content_right_tt_gold_box_width;
          padding: $email_box_content_right_tt_gold_box_padding;
          border-radius: $email_box_content_right_tt_gold_box_radius;
          &_icon {
            color: $email_box_content_right_tt_gold_icon_color;
            margin: $email_box_content_right_tt_gold_icon_margin;
          }
          &_text {
            font-size: $email_box_content_right_tt_gold_icon_font_size;
          }
        }
      }

      &_bt {
        font-size: $email_box_content_rigth_bt_font_size;
        color: $email_box_content_rigth_bt_color;
        margin: $email_box_content_rigth_bt_margin;
        word-break: break-all;
        @include set_text_ellipsis(2);

        &.all_text {
          @include set_text_ellipsis(99);
        }
      }
    }
  }

  .box-link {
    margin: $email_box_content_link_margin;
    font-size: $email_box_content_link_font_size;
    color: $email_box_content_link_color;
  }

  .box-view {
    margin: $email_box_content_box_view_margin;
    padding: $email_box_content_box_view_padding;
    background-color: $email_box_content_box_view_bg;
    @include setBox(row, space-between, center);

    &-title {
      @include setBox(row, space-between, center);
    }
    &-icon {
      margin: $email_box_content_box_view_gold_icon_margin;
      color: $email_box_content_box_view_gold_icon_color;
    }
    &-text {
      font-size: $email_box_content_box_view_text_font_size;
      font-weight: $email_box_content_box_view_text_font_weight;
      font-style: $email_box_content_box_view_text_font_style;
    }
    &-btn {
      font-size: $email_box_content_box_view_btn_font_size;
      margin: $email_box_content_box_view_btn_margin;
    }
  }
  .bottom_box {
    padding: $email_box_bottom_box_padding;
    border-top: thin solid $email_box_bottom_box_border_color;
    @include setBox(row, space-between, center);
    &_title {
      color: $email_box_bottom_box_title_color;
      font-size: $email_box_bottom_box_title_font_size;
    }
    .btn_show {
      @include setBox(row, center, center);
      font-size: $email_box_bottom_box_btn_font_size;
      color: $email_box_bottom_box_btn_color;
      transition: opacity 0.25s;
      &:hover {
        opacity: 0.86;
      }
      &_text {
        margin: $email_box_bottom_box_btn_text_margin;
      }
    }
  }
}

@include breakpoint-only-up() {
  .mes_box {
    border-radius: $email_box_pc_radius !important;
  }
}
</style>
