<template>
  <div class="column_flex email_wrap" style="height: 100%" app>
    <template v-if="isApp">
      <back-bar :leftArrow="true" :backText="$t('UI_Message_Center')" @back="changeEmailState"></back-bar>
    </template>
    <template v-else>
      <div class="email_head_top"></div>
      <div class="email_head_pc w_100" @click="onRightDrawer">
        <span>{{ $t('UI_Message_Center') }}</span>
        <icon-font
          class="cursor_point"
          name="icon-icon_email_arrow"
          dark="t_text_color_2"
          :size="variables.email_list_head_pc_arrow_size"
        />
      </div>
    </template>
    <div v-if="tabList.length" class="email_caht_tab">
      <email-tab :tabList="tabList" :tabTndex="tabTndex" @emailHall="emailHall"></email-tab>
    </div>
    <template v-if="tabTndex == 0">
      <chat-integrate :badge="aiChatDot" />
    </template>
    <template v-if="tabTndex == 1">
      <list-view
        :is-default="list.length == 0"
        :is-load="isLoad"
        :is-shake="isShake"
        @nextPage="onNextPage"
        class="list_wrap"
      >
        <email-box
          :item="item"
          v-for="(item, i) in list"
          :key="i"
          @getReSetList="getReSetList()"
          @submit="onReceive(item)"
        ></email-box>
        <!-- loading -->
        <div class="loading" v-if="!isLoad">
          <v-progress-circular
            width="3"
            :size="variables.email_list_loading_size"
            :indeterminate="true"
          ></v-progress-circular>
        </div>
      </list-view>
    </template>

    <div class="list_foot email_safe_area_inset_bottom" v-if="tabTndex == 1">
      <v-btn @click="onDelete" :disabled="!list.length" class="foot_btn border_color" outlined>
        {{ $t('UI_Email_del') }}
      </v-btn>
      <v-btn @click="onReceiveAll" :disabled="!list.length" color="t_btn_host_color" class="foot_btn primary_disabled">
        {{ $t('UI_Email_Receber') }}
      </v-btn>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex'
import EmailBox from './email-box'
import chatIntegrate from './chat-integrate'
import EmailTab from './email-tab'
import ListView from '@/components/ui/list-view'
import BackBar from '@/components/ui/back-bar'
import loadIcon from '@/assets/icons'
import PublishSubscribe from 'publish-subscribe-js'
import { lodash } from '@/utils/helper'
import { getMessage, receive, oneClickReceive, oneClickDelete } from '@/api/modules/config'
import variables from '@style/components/game-container.email-modal.modules.scss'

export default {
  components: {
    EmailBox,
    ListView,
    BackBar,
    EmailTab,
    chatIntegrate
  },
  data() {
    return {
      variables,
      list: [],
      page: 1,
      limit: 10,
      isLoad: true, // 列表是否加载完成
      isShake: false, // 防抖
      tabTndex: 1,
      tabList: []
    }
  },
  computed: {
    ...mapState('modal', ['emailState', 'isPromotionKefu', 'rightMenu']),
    ...mapState('base', ['isApp', 'user', 'languageType', 'isFiring', 'systemConfig']),
    ...mapGetters('base', ['chatPromoteSwitch', 'getDotEntry', 'isTourist']),
    inboxDot() {
      return this.getDotEntry('mail').value
    },
    aiChatDot() {
      return this.getDotEntry('ai_promote').value
    }
  },
  watch: {
    chatPromoteSwitch: {
      handler(val) {
        if (!val) {
          this.tabTndex = 1
          this.tabList = []
          return
        }
        this.tabTndex = 0
        this.tabList = [
          {
            lable: this.$t('UI_Cooperation_service'),
            value: 0,
            iconName: 'icon-Service_email',
            showDot: this.aiChatDot
          },
          {
            lable: this.$t('UI_Inbox'),
            value: 1,
            iconName: 'icon-InBox_2',
            showDot: this.inboxDot
          }
        ]
      },
      immediate: true
    },
    inboxDot(val) {
      if (this.chatPromoteSwitch && this.tabList.length) {
        this.tabList[1].showDot = val
      }
    },
    aiChatDot(val) {
      if (this.chatPromoteSwitch && this.tabList.length) {
        this.tabList[0].showDot = val
      }
    },
    // 监听是否为游客
    isTourist(n) {
      if (n) this.list = []
    },
    // 项目是否启动成功
    isFiring(newValue = false) {
      if (newValue) this.getList()
    },
    isPromotionKefu(newValue = false) {
      // 处理活动跳转推广客服
      if (newValue) {
        this.tabTndex = 0
        this.$store.dispatch('modal/setIsPromotionKefu', false)
      }
    }
  },
  created() {
    if (this.isFiring) this.getList()
  },
  methods: {
    // 图片显示
    loadIcon(key) {
      return loadIcon(key, false)
    },
    // 获取列表数据
    async getList() {
      const params = {
        token: this.user.token,
        page: this.page,
        limit: this.limit,
        language: this.languageType
      }
      // this.$pop.loading().show();
      this.isShake = true
      const { code, data } = await getMessage(params).catch(e => e)
      this.isShake = false
      // this.$pop.loading().hide();
      if (code != 0) return

      let listData = []
      if (data && Array.isArray(data)) {
        listData = data
      } else if (lodash.isObject(data)) {
        listData = Object.values(data)
      }

      this.list = this.list.concat(listData)
      // 是否加载完成，如果返回数据条数少于需要的条数，就是加载完成了
      this.isLoad = listData.length < this.limit
    },
    // 下一页
    onNextPage() {
      this.page++
      this.getList()
    },
    // 接收单个赠送
    async onReceive(item = {}) {
      const { id = '' } = item
      const params = { token: this.user.token, language: this.languageType, id }

      const { code = 0, msg = '' } = await receive(params).catch(e => e)

      // 更新红点信息
      this.$store.dispatch('base/showRedDotReceivedFun', {
        version: 1,
        event: 'mail',
        language: this.languageType,
        token: this.user.token
      })

      if (code != 0) {
        if (code == 5001) {
          this.page = 1
          this.list = []
          this.getList()
          return this.$pop.message().show({ content: msg })
        } else {
          return this.$pop.message().show({ content: msg })
        }
      }

      // 插入弹窗序列
      PublishSubscribe.publish(this.$store.state?.dialog?.DUALOG_MANAGER_KEY, {
        key: 'deposit',
        seat: 'first',
        params: {
          title: this.$t('UI_Email_Rewards'),
          value: item.gold || 0
        }
      })

      // 手动更改状态 status 0 未读 1已读未领 2已读已领
      item.status = 2
    },
    // 删除邮件
    onDelete() {
      if (this.list.length < 1) return
      this.$pop
        .confirm({
          title: this.$t('General.General_9'),
          confirmText: this.$t('General.General_1'),
          cancelText: this.$t('General.General_2'),
          content: this.$t('UI_Email_mailsDel'),
          success: async () => {
            const params = { token: this.user.token, language: this.languageType }
            const { code = 0, msg = '' } = await oneClickDelete(params).catch(e => e)

            // 更新红点信息
            this.$store.dispatch('base/showRedDotReceivedFun', {
              version: 1,
              event: 'mail',
              language: this.languageType,
              token: this.user.token
            })

            if (code != 0) {
              if (code == 5001) {
                this.page = 1
                this.list = []
                this.getList()
                return this.$pop.message().show({ content: msg })
              } else {
                return this.$pop.message().show({ content: msg })
              }
            }
            this.page = 1
            this.list = []
            this.getList()
          }
        })
        .show()
    },
    // 接收全部邮件
    async onReceiveAll() {
      if (this.list.length < 1) return
      const params = { token: this.user.token, language: this.languageType }
      const { code = 0, data = {}, msg = '' } = await oneClickReceive(params).catch(e => e)

      // 更新红点信息
      this.$store.dispatch('base/showRedDotReceivedFun', {
        version: 1,
        event: 'mail',
        language: this.languageType,
        token: this.user.token
      })

      if (code !== 0) {
        if (code == 5001) {
          this.page = 1
          this.list = []
          this.getList()
          return this.$pop.message().show({ content: msg })
        } else {
          return this.$pop.message().show({ content: msg })
        }
      }

      // 插入弹窗序列
      PublishSubscribe.publish(this.$store.state?.dialog?.DUALOG_MANAGER_KEY, {
        key: 'deposit',
        seat: 'first',
        params: {
          title: this.$t('UI_Email_Rewards'),
          value: data.gold || 0
        }
      })

      this.page = 1
      this.list = []
      this.getList()
    },
    // 关闭整个弹窗
    changeEmailState() {
      this.$store.dispatch('modal/setEmailState', !this.emailState)
    },
    // pc端右侧关闭
    onRightDrawer() {
      this.$store.dispatch('modal/setRightMenu', !this.rightMenu)
    },
    emailHall(item) {
      const { value = 0 } = item
      this.tabTndex = value
      // 处理活动跳转推广客服
      this.$store.dispatch('modal/setIsPromotionKefu', false)
    },
    // 重置邮件列表
    getReSetList() {
      this.page = 1
      this.list = []
      this.getList()
    }
  }
}
</script>

<style lang="scss" scoped>
@import '@style/components/game-container.email-modal.modules.scss';

.email_wrap {
  background: $email_list_wrap_bg;
}

.email_gold_icon_collor {
  color: $email_list_icon_color;
}

.border_color {
  border-color: $email_list_foot_btn_outlined_border_color;
}

.email_head_top {
  height: $email_head_top_h;
  background-color: $top_nav_b;
  width: 100%;
}

.email_head_pc {
  @include setBox(row, space-between, center);
  border-top: thin solid $email_list_head_pc_border_color;
  border-bottom: thin solid $email_list_head_pc_border_color;
  // margin: $email_list_head_pc_margin;
  padding: $email_list_head_pc_padding;
  font-size: $email_list_head_pc_font_size;
}

.primary_disabled.v-btn:disabled {
  opacity: 0.3;
  background-color: $btn_host_color !important;
  color: #fff !important;
}

.list_foot {
  width: 100%;
  box-sizing: border-box;
  @include setBox(row, space-between, center);
  border-top: thin solid $email_list_foot_border_top_color;
  padding: $email_list_foot_padding;

  .foot_btn {
    width: $email_list_foot_btn_width;
    font-size: $email_list_foot_btn_font_size;
  }
}

.list_wrap {
  padding: $email_list_padding;
}

.loading {
  padding: $email_list_loading_padding;
}

.email_safe_area_inset_bottom {
  height: $email_list_foot_height !important;
  height: calc(#{$email_list_foot_height} + constant(safe-area-inset-bottom)) !important;
  /* 直接扩展高度，因为padding-bottom是内边距 */
  height: calc(#{$email_list_foot_height} + env(safe-area-inset-bottom)) !important;
  /* 直接扩展高度 */
  /* 直接扩展高度 */
  box-sizing: border-box;
  padding-bottom: constant(safe-area-inset-bottom);
  padding-bottom: env(safe-area-inset-bottom);
}
.email_caht_tab {
  width: 100%;
  padding: 0px 16px;
}
</style>
